@use '../../utils/variables' as v;

.menu-container {
  background-color: v.$paleSilver;
  height: 100%;
  width: 60%;
  display: flex;
  align-self: center;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  @media screen and (max-width: v.$medium) {
    width: 100%;
  }

  &__logo {
    width: 300px;
    border-radius: 50%;
    @media screen and (max-width: v.$small) {
      width: 200px;
    }
    padding: 20px;
  }

  &__title {
    font-family: QwigleyRegular;
    font-size: 64px;
    margin: 0;
    color: v.$bistre;
  }

  &__menu-table {
    overflow-y: scroll;
    width: 80%;
    padding: 0 10%;
    display: flex;
    flex-direction: column;
    margin: 20px 0;
  }

  &__ribbon {
    --f: 10px; /* control the folded part*/
    --r: 15px; /* control the ribbon shape */
    --t: 10px; /* the top offset */

    padding: 10px 20px;
    clip-path: polygon(
      0 0,
      100% 0,
      calc(100% - var(--r)) 50%,
      100% 100%,
      0% 100%,
      calc(var(--r)) 50%
    );
    background: v.$bistre;
    margin: 10px 0;
    text-align: center;
    width: 50%;
    align-self: center;
    font-family: SignikaBold;
    color: v.$white;
  }

  &__subitems-container {
    display: flex;
    justify-content: space-between;
    & span {
      //   line-height: 40px;
      font-family: PalanquinDarkRegular;
      color: v.$bistre;
    }
    margin: 10px 0;
    height: 20px;
    align-items: center;
  }

  &__divider {
    border-bottom: #bd1550 dotted 1px;
    flex-grow: 1;
    height: 20px;
  }

  &__button {
    height: fit-content;
    padding: 1px;
    border-radius: 5px;
    margin-left: 5px;
    cursor: pointer;
    border: 2px solid v.$bistre;
    background-color: v.$mimiPink;
    & svg {
      color: v.$bistre;
    }
    transition: scale 0.3s;
  }

  &__button:hover {
    transform: scale(1.1);
  }
}

.modal {
  &__body {
    background-color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 10px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    & img {
      height: 200px;
      border-radius: 10px;
    }
    & span {
      font-family: PalanquinDarkRegular;
      color: v.$bistre;
    }
  }
  &__title {
    margin-left: 10px;
    font-size: 24px;
  }
  &__image-container {
    display: flex;
    flex-direction: row;
  }
  &__image-item {
    display: flex;
    flex-direction: column;
    margin: 0 10px 10px;
  }
}
