@use '../../utils/variables' as v;

.orders-container {
  background-image: url('../../assets/orders-background-desktop.jpg');
  height: 100%;
  width: 100%;
  background-size: cover;
  display: flex;
  align-self: center;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  @media screen and (max-width: v.$small) {
    background-image: url('../../assets/orders-background-mobile.jpg');
  }

  &__logo {
    width: 150px;
    border-radius: 50%;
  }

  &__title {
    font-family: QwigleyRegular;
    font-size: 120px;
    color: v.$bistre;
    padding: 30px;
    border-radius: 50%;
    background-color: rgba(red(v.$paleSilver), green(v.$paleSilver), blue(v.$paleSilver), 0.7);
    @media screen and (max-width: v.$small) {
      font-size: 48px;
    }
  }
}
