@use '../../utils/variables' as v;

.home-container {
  background-color: v.$paleSilver;
  height: 100%;
  width: 60%;
  display: flex;
  align-self: center;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  @media screen and (max-width: v.$medium) {
    width: 100%;
  }

  &__logo {
    width: 300px;
    border-radius: 50%;
    @media screen and (max-width: v.$small) {
      width: 200px;
    }
  }

  &__link-section {
    display: flex;
    flex-direction: column;
  }

  &__link-button {
    padding: 10px;
    text-decoration: none;
    color: v.$white;
    margin: 10px 0;
    background-color: v.$lightCoral;
    border-radius: 15% 33% 12% 6% / 50% 24% 92% 100%;
    text-align: center;
    font-family: QwigleyRegular;
    font-size: 32px;
    transition: scale 0.2s;
  }

  &__link-button:hover {
    scale: (1.1);
  }
}
